// Card componat
function LoadTutrials() {
  const [tutrials, setTutrials] = useState([])
  useEffect(() => {
    setTutrials(tutrialsData)
  }, [])

  return (
    <div className="card">
      {
        tutrials.map(tutrial=><Tutrials head={tutrial.title} body={tutrial.desc}></Tutrials>)
      }
    </div>

  )
}

function Tutrials(props) {
  return (
    <div className="single-card">
      <h2>{props.head}</h2>
      <p>{props.body}</p>
      <button>View details</button>
    </div>
  )
}








